import React, { useEffect, useState } from "react";

const Topbar = () => {
  const [user, setUser] = useState(null);
  const [renderContent, setRenderContent] = useState(
    <a href="/signin">Sign In</a>
  );

  const fetchUser = async () => {
    let fetchRes = await fetch("/loginstatus", { method: "GET" });
    let fetchJson = await fetchRes.json();
    setUser(fetchJson["username"]);
  };

  const onSignout = async () => {
    localStorage.removeItem("user");
    await fetch("/signout", { method: "GET" });
    window.location.href = "/";
  };

  useEffect(() => {
    fetchUser();
  }, []);

  useEffect(() => {
    if (user === null) {
      setRenderContent(<a href="/signin">Sign In</a>);
    } else {
      setRenderContent(
        <>
          <a href="/#">
            <b>{user}</b>
          </a>
          <a href="/" onClick={onSignout}>
            Sign Out
          </a>
        </>
      );
    }
  }, [user]);

  console.log(user);

  return (
    <div className="Topbar">
      <div className="toggleBars">
        <div className="bar" />
        <div className="bar" />
        <div className="bar" />
      </div>
      <div className="logo" />
      <div className="Navlinks">
        <a href="/">Home</a>
        <a href="/daily_itinerary">Daily Itinerary</a>
        <a href="/search">Search</a>
        {renderContent}
      </div>
    </div>
  );
};

export default Topbar;
